import '../styles/header.css'
import {useState, useEffect} from 'react'

function Header(){

    const [title, setTitle] = useState('កម្មវិធី React')
    const [data, setData] = useState(false)
    const [q, setQ] = useState(0)
    const [category, setCategory] = useState(0)

    function handleSubmit(e){
        e.preventDefault()
        setData([q, category])
    }

    useEffect(()=>{
        if(data){
            alert(data[0])
            alert(data[1])
        }
    })

    return (
        <div className="Header ">
            <div class='wrapper region'>
                <div class='logo' onMouseOver={()=>setTitle('កម្មវិធី​កំពុង​ដំណើរការ')} onMouseOut={()=>setTitle('កម្មវិធី React')}>
                    <a>{title}</a>
                </div>

                <form class='search' onSubmit={handleSubmit}>
                    <select name='category' onChange={(e)=>setCategory(e.target.value)}>
                        <option>ការផ្សាយ</option>
                        <option>ជំពូក</option>
                    </select>
                    <input type="text" name='q' onChange={(e)=>setQ(e.target.value)} required />
                    <input type='submit' value='បញ្ជូន' />
                </form>

                <div class='logout'>ទំព័រ​ដើម | ចូល​ក្នុង</div>
            </div>
        </div>
    )
}

export default Header